import React, {Component} from "react";
import {connect} from "react-redux";
import OraSwitcher from "eshop/modules/core/components/ui/OraSwitcher"

import {
    getMarketContext,
    getPropositionListCount,
    getPropositionListCountSetMode,
    getPropositionListSoftBundleGroup,
    getSimCountSelected,
    getVerifiedMsisdnB2B,
} from "eshop/modules/configurator/selectors/filters"
import {
    checkMsisdnAndGetOffersB2B,
    clearCheckMsisdnB2B,
    propositionListCountDecrement,
    propositionListCountIncrement,
    propositionListCountSet,
    propositionListCountSetMode,
    selectLoyaltyLengthB2B,
    selectProcessTypeB2B,
    setMaxSimCount,
} from "eshop/modules/configurator/actions/filters"
import {getVerifyMsisdnLoading} from "eshop/modules/configurator/selectors/metaData"
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getCartEntries, getCartIsNet, isCartMobile} from "eshop/modules/cart/selectors";
import BundleTypeEnum from "../../../cart/components/entriesList/BundleTypeEnum";
import {redirectToLastViewedOfferPage, removeFromCart} from "eshop/modules/cart/actions/cart";
import {OraButton} from "eshop/components/OraCommonComponents";
import {
    getSelectedSoftBundleGroup,
    isAccessoryListPage,
    isProductDetailsPage,
    isProductListPage,
} from "eshop/modules/simfree/selectors"
import MarketSegment from "../../../core/enum/MarketSegment";
import SalesChannel from "../../../core/enum/SalesChannel";

class MobileSimCountSelectionComponent extends Component {

    constructor(props) {
        super(props);
    }

    componentWillMount() {
        if (this.props.channels.sales !== SalesChannel.WWW && MarketSegment.isB2B(this.props.marketContext))
            this.props.propositionListCountSetMode(true);
    }

    componentDidMount() {

        this.props.setMaxSimCount(this.props.maxSimCount);
        if (!this.props.isProductDetailsPage && !this.props.isProductListPage)
            OnlineUtils.setAsLastViewedOfferPage();
    }

    componentDidUpdate() {
        if ((this.props.isCartMobile && this.props.cartIsNet) || !MarketSegment.isB2B(this.props.marketContext)) {
            console.log("$$$$$$$$$$$$$$$$$$$$componentDidUpdate");
            this.propositionListCountSetMode();
        }

        if (this.cardsCountRef) {
            OPL.UI.loadModules(this.cardsCountRef, this.oplLayoutFixerModule())
            this.updateDimensions()
        }
    }

    componentWillUnmount() {
        if (this.cardsCountWrapperRef) {
            OPL.UI.stopModules(this.cardsCountWrapperRef)
        }
    }

    oplLayoutFixerModule() {
        return ({
                path: "core/modules/layout-fixer",
                options: {
                    selectors: [
                        ".js-layout-fixer-group-sim-count-select"
                    ]
                }
            }
        )
    }

    updateDimensions() {
        OPL.UI.fire(OPL.UI.EVENTS.modules.layoutFixer.recalculate, this.cardsCountRef);
    }

    propositionListCountSetMode() {
        console.log(this.props);
        console.log("***********************= isPropositionListCountSetMode(false)", this.props.isPropositionListCountSetMode)
        this.props.propositionListCountSetMode(false)
    }

    setSimCount(value) {
        this.props.setSIMCount(value);
        if (!this.props.isB2B) {
            this.propositionListCountSetMode();
        }
    }

    render() {
        if (this.props.isProductListPage || this.props.isProductDetailsPage || this.props.isAccessoryListPage) {
            return (<SimCountLoopMonitor {...this.props}/>)

        }


        let mobileEntries = this.props.entries && this.props.entries.filter(ent => {
            return ent.bundleType === BundleTypeEnum.VOICE || ent.bundleType === BundleTypeEnum.DATA
        });

        if (!!(mobileEntries && mobileEntries.length)
            || (this.props.propositionListSoftBundleGroup
                && this.props.selectedSoftBundleGroup != this.props.propositionListSoftBundleGroup)) {
            return null;
        }
        let countOverflow = (parseInt(this.props.descriptions.simBoxCount) - 1) || 3;
        let simCardTiles = [];

        let mediumCol = 3;
        let largeCol = 3;
        if (countOverflow >= 5) {
            mediumCol = 2;
            largeCol = 2;
        }

        for (let i = 1; i <= countOverflow; i++) {
            simCardTiles.push(<SIMCountBox key={"simCountBox" + i}
                                           boxClassName={" u-small-margin-l u-medium-margin-l  l-col l-col-small-6 l-col-medium-" + mediumCol + " l-col-" + largeCol}
                                           index={i} propositionListCount={this.props.propositionListCount}
                                           setSimCount={this.setSimCount.bind(this)}/>);
        }

        let showSimCardTiles = ((!mobileEntries || mobileEntries.length === 0)
            && (!this.props.isB2B || (this.props.channels.sales === SalesChannel.WWW && this.props.isB2B)))
            || (this.props.isPropositionListCountSetMode && this.props.isB2B);
        console.warn([{showSimCardTiles}, this.props.isPropositionListCountSetMode, {mobileEntries}]);
        return (
            <div className="l-full-row g-gray1-bg">
                <div className="l-page-row u-padding-l u-padding-top-l">

                    {!showSimCardTiles && this.props.descriptions.legalSimCountSelectionNotVisible &&
                    <div>
                        <div
                            dangerouslySetInnerHTML={{__html: this.props.descriptions.legalSimCountSelectionNotVisible}}/>


                    </div>
                    }

                    {showSimCardTiles &&
                    <div ref={ref => this.cardsCountWrapperRef = ref}>
                        {this.props.descriptions.title && <h2>{this.props.descriptions.title}</h2>}

                        {this.props.descriptions.mainInfo &&
                        <div dangerouslySetInnerHTML={{__html: this.props.descriptions.mainInfo}}/>}

                        <ul className="l-row u-padding-l" ref={ref => this.cardsCountRef = ref}>
                            {simCardTiles}
                            <SIMCountInput key={"simCountInput"}
                                           boxClassName={" u-small-margin-l u-medium-margin-l l-col l-col-small-6 l-col-medium-" + mediumCol + " l-col-" + largeCol} {...this.props}
                                           countOverflow={countOverflow} setSimCount={this.setSimCount.bind(this)}/>
                        </ul>
                        {this.props.descriptions.legalSimCountSelectionVisible && <div
                            dangerouslySetInnerHTML={{__html: this.props.descriptions.legalSimCountSelectionVisible}}/>}

                        {this.props.isB2B && this.props.channels.sales !== SalesChannel.WWW &&
                        <div className="l-page-row u-padding-l-xl u-small-padding-l u-medium-padding-s u-padding-s">
                            <div className="l-row">
                                <div
                                    className="l-col l-col-small-12 l-col-medium-6 l-col-3 u-padding-top-l u-padding-l u-right">
                                    <button className="opl-btn opl-btn--medium opl-btn--primary o-btn u-w-100"
                                            onClick={this.propositionListCountSetMode.bind(this)}>
                                        Dalej
                                    </button>
                                </div>

                            </div>
                        </div>
                        }
                    </div>
                    }
                </div>
            </div>
        )

    }
}

class SimCountLoopMonitor extends React.Component {


    render() {
        if (this.props.simCountSelected < 2) return null;
        return (

            <div className={"l-full-row g-white1-bg "} id={"simCountLoopMonitor"}>

                <div className={"l-page-row u-padding-l "}>
                    <div className={"u-padding-all-l u-box-shadow--s "}>
                        <div
                            className="l-row ">
                            <div
                                className="l-col l-col-6 l-col-medium-12 l-col-small-12 ">
                                <h3>{this.props.descriptions.header||"Tworzysz ofertę złożoną z"}<span
                                    className={"g-brand1-c"}> {this.props.simCountSelected} {this.props.simCountSelected>1?"kart":"karty"} SIM</span>
                                </h3>
                                <p>Skonfigurowane abonamenty
                                    komórkowe - {this.props.simCountSelected - this.props.propositionListCount}.
                                    Pozostało do konfiguracji - {this.props.propositionListCount}</p>
                                <p>
                                    <a href={"#"}
                                       onClick={this.props.removeFromCart}>
                                        {(this.props.channels.sales === SalesChannel.WWW
                                            ? this.props.descriptions.removeCartButtonWWW
                                            : this.props.descriptions.removeCartButton) || "Wyczyść koszyk"}
                                    </a>

                                </p>
                            </div>
                            <div className={"l-col l-col-6 l-col-medium-12 l-col-small-12 u-small-padding-top-l"}>
                                <OraButton className="u-right" type="primary"
                                           onClick={this.props.redirectToLastViewedOfferPage}>
                                    {(this.props.channels.sales === SalesChannel.WWW
                                        ? this.props.descriptions.continueConfigurationLabelWWW
                                        : this.props.descriptions.continueConfigurationLabel) || "Dokończ konfigurację"}
                                </OraButton></div>
                        </div>
                    </div>
                </div>
            </div>)
    }
}


class SIMCountInput extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: this.props.countOverflow + 1
        };
    }

    componentDidMount() {
        OPL.UI.loadModules(this.refInput, [
            {
                path: 'common/modules/opl-input-mask',
                options: {"mask": "99", "condition": "seen"}
            }
        ]);
    }

    setSimCount(value) {
        if (value) {
            if (parseInt(value) > this.props.maxSimCount) {
                this.props.setSIMCount(this.props.maxSimCount);
                this.setState({count: this.props.maxSimCount});
                value = this.props.maxSimCount;
                //error
                return value;
            }
            this.props.setSIMCount(value);
            this.setState({count: value});
            return value;
        }


    }

    onKeyDown(event) {
        if (event.keyCode == 13) {
            this.refInput.value = this.setSimCount(event.currentTarget.value);
            this.refInput.blur();
        }
    }

    onChange(event) {
        event.currentTarget.value = this.setSimCount(event.currentTarget.value);
    }

    componentDidUpdate() {
        if (parseInt(this.props.countOverflow) >= parseInt(this.props.propositionListCount)) {
            this.hideInput();
        } else {
            this.showInput();
        }
    }

    hideInput() {
        var linkWrapper = $('#addCard__link'),
            inputWrapper = $('#addCard__input');
        linkWrapper.removeClass('u-hide');
        inputWrapper.addClass('u-hide');
    }

    showInput() {
        var linkWrapper = $('#addCard__link'),
            inputWrapper = $('#addCard__input');
        linkWrapper.addClass('u-hide');
        inputWrapper.removeClass('u-hide');
    }

    onClick(e) {
        //
        this.showInput();
        e.preventDefault();
        e.stopPropagation();
        let that = this;
        if (this.props.propositionListCount < this.props.countOverflow) {
            this.refInput.value = this.props.countOverflow + 1;
        }
        this.props.setSimCount(this.refInput.value);
        setTimeout(function () {
            that.refInput.select()
        }, 200)
        ;

    }

    onInputClick(e) {
        e.preventDefault();
        e.stopPropagation();
        this.refInput.select();

    }

    render() {
        return (

            <OraSwitcher
                className={this.props.boxClassName}
                onChangeHandler={this.onClick.bind(this)}
                isSelected={this.props.propositionListCount > this.props.countOverflow}
                switcherClassName="switcher">
                <div
                    className="u-position-relative u-small-no-margin u-medium-no-margin u-no-margin u-text-center g-white1-bg u-box-shadow--s u-cursor-pointer">
                    <div className="opl-selection-layer" aria-hidden="true">
                        <div className="opl-selection-layer__border"/>
                        <div className="opl-selection-layer__checkmark"/>
                    </div>
                    <div>
                        <label htmlFor="switcher_trigger-input"
                               className="u-small-no-padding u-medium-no-padding-right u-medium-no-padding-left u-medium-padding-top-l u-medium-padding-l u-padding-all-xxl u-w-50 u-text-center">
                            <div className="u-animate-height js-layout-fixer-group-sim-count-select">
                                <div className="js-height-sensitive-element">
                                    <div id="addCard__link" className={this.state.inputVisible ? "u-hide" : ""}>
                                        <a className="opl-product-ghostlink--number u-margin u-padding-top-l" href="#"
                                           data-number="+">
                                            <span className="g-icon g-icon--sim g-icon--only"/>
                                            <span className="u-acc-hide">Więcej</span>
                                        </a>
                                        <p className="u-font-bold h3 u-padding-top-s u-padding-m u-letter-spacing-s u-no-margin u-small-hide u-medium-hide"
                                           aria-hidden="true">Więcej kart</p>
                                        <p className="u-font-bold h3 u-padding-top-s u-padding-m u-letter-spacing-s u-no-margin u-large-hide"
                                           aria-hidden="true">Więcej</p>
                                    </div>
                                    <div id="addCard__input" className={this.state.inputVisible ? "" : "u-hide"}>
                                        <label
                                            className="u-font-bold g-white1-bg u-small-padding-top-s u-small-padding-s u-padding-top-l u-padding-l-xl">
                                            <p className="h5 u-block u-small-margin-top-s u-margin">Wpisz liczbę</p>
                                            <input className="opl-input--size-m h1 u-text-center u-no-margin"
                                                   type="text" defaultValue={this.props.propositionListCount}
                                                   onClick={this.onInputClick.bind(this)}
                                                   autoComplete={"off"} ref={ref => this.refInput = ref}
                                                   onBlur={this.onChange.bind(this)}
                                                   onKeyDown={this.onKeyDown.bind(this)}/>
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </label>
                    </div>
                </div>
            </OraSwitcher>
        )
    }
}


class SIMCountBox extends React.Component {
    onClick() {
        this.props.setSimCount(this.props.index);
    }

    render() {
        return (
            <OraSwitcher
                className={this.props.boxClassName}
                onChangeHandler={this.onClick.bind(this)}
                isSelected={this.props.propositionListCount == this.props.index}
                switcherClassName="switcher">
                <div
                    className="u-position-relative u-small-no-margin u-medium-no-margin u-no-margin u-text-center g-white1-bg u-box-shadow--s u-cursor-pointer">
                    <div className="opl-selection-layer" aria-hidden="true">
                        <div className="opl-selection-layer__border"/>
                        <div className="opl-selection-layer__checkmark"/>
                    </div>
                    <label
                        className="u-small-no-padding u-medium-no-padding-right u-medium-no-padding-left u-medium-padding-top-l u-medium-padding-l u-padding-all-xxl u-w-50 u-text-center"
                        htmlFor={"switcher_trigger-" + this.props.index}>
                        <div className="u-animate-height js-layout-fixer-group-sim-count-select">
                            <div className="js-height-sensitive-element">
                                <a className="switcher-subtrigger opl-product-ghostlink--number u-margin u-padding-top-l"
                                   data-number={this.props.index}>
                                    <span className="g-icon g-icon--sim g-icon--only"/>
                                </a>
                                <p className="u-font-bold h3 u-padding-top-s u-padding-m u-letter-spacing-s u-no-margin">{this.props.index}
                                    <span> {simCountInflexion(this.props.index)}</span>
                                </p>
                            </div>
                        </div>
                    </label>
                </div>
            </OraSwitcher>
        )
    }
}

function simCountInflexion(index) {
    switch (index) {
        case 1:
            return "karta";
        case 2:
            return "karty";
        case 3:
            return "karty";
        case 4:
            return "karty";
        case 5:
            return "kart";
    }
}

const mapStateToProps = state => ({
    propositionListCount: getPropositionListCount(state),
    simCountSelected: getSimCountSelected(state),
    isPropositionListCountSetMode: getPropositionListCountSetMode(state),
    cartIsNet: getCartIsNet(state),
    entries: getCartEntries(state),
    isCartMobile: isCartMobile(state),
    isB2B: getMarketContext(state) === "B2B",
    verifiedMsisdn: getVerifiedMsisdnB2B(state),
    verifyMsisdnLoading: getVerifyMsisdnLoading(state),
    isProductListPage: isProductListPage(state),
    isAccessoryListPage: isAccessoryListPage(state),
    isProductDetailsPage: isProductDetailsPage(state),
    selectedSoftBundleGroup: getSelectedSoftBundleGroup(state),
    propositionListSoftBundleGroup: getPropositionListSoftBundleGroup(state),
});

const mapDispatchToProps = dispatch => ({
    setSIMCount: count => dispatch(propositionListCountSet(count)),
    propositionListCountSetMode: on => dispatch(propositionListCountSetMode(on)),
    increment: () => dispatch(propositionListCountIncrement()),
    decrement: () => dispatch(propositionListCountDecrement()),
    setMaxSimCount: masSimCount => dispatch(setMaxSimCount(masSimCount)),
    selectProcessType: (processType, index) => dispatch(selectProcessTypeB2B(processType, index)),
    selectLoyaltyLength: (loyaltyLength, index) => dispatch(selectLoyaltyLengthB2B(loyaltyLength, index)),
    verifyMsisdn: (msisdn, index) => dispatch(checkMsisdnAndGetOffersB2B(msisdn, index)),
    clearCheckMsisdn: index => dispatch(clearCheckMsisdnB2B(index)),
    removeFromCart: () => dispatch(removeFromCart(null)),
    redirectToLastViewedOfferPage: () => dispatch(redirectToLastViewedOfferPage()),
});

export default connect(mapStateToProps, mapDispatchToProps)(MobileSimCountSelectionComponent)